// colors
$gutes-color-one: #103a5d;
$gutes-color-two: #d35400;
$gutes-color-three: #333333;

$gutes-color-brand: $gutes-color-one;
$gutes-color-default: #333;
$gutes-color-primary: $gutes-color-brand;
$gutes-color-secondary: $gutes-color-two;

// font and font-colors
$gutes-base-font-color: #333333;
$gutes-base-font-size--xs: 0.875rem;
$gutes-base-font-size--xl: 1.125rem;
$gutes-base-font-size: 1rem;
$gutes-base-line-height: 1.3;
$gutes-base-font-weight: 400;
$gutes-base-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Helvetica Neue", Arial, sans-serif;

// Base Border Settings
$gutes-base-border-radius: 4px;
$gutes-base-border-width: 1px;
$gutes-base-border-color: var(--color-gray-light);

// misc
$gutes-size-tag-gutter: 5px;
$gutes-grid-gap: 2rem;
$gutes-element-bgcolor: #ebebeb;


//tag-filter
$gutes-tag-filter-border-color: #333333;
$gutes-tag-filter-border-color-checked: #d35400;
$gutes-tag-filter-bgcolor: #ffffff;

// Base Spacing Units
$gutes-base-spacing-unit: 1rem;

// detail page - hero
$gutes-detail-hero-overlay-color: rgba(#333, .3);
$gutes-detail-hero-title-color: #fff;
$gutes-detail-hero-text-shadow: 0 0 10px #000;
$gutes-detail-hero-text-shadow: 0 0 10px #000;

// detail page - video
// Video
$gutes-video-bgcolor: #ebebeb;

//  List Styles
//$gutes: var(--gutes-box-shadow);
$gutes-box-shadow: 0 0 10px rgba(#000, .5);

// title
$gutes-font-size-title: 1.5rem;
$gutes-font-weight-title: normal;

// Base Border Settings
$gutes-base-border-radius: 4px;
$gutes-base-border-width: 1px;
$gutes-base-border-color: var(--color-gray-light);

// click and collect
$gutes-clickcollect-bgcolor: #e67e22;
$gutes-clickcollect-color: #fff;

html {

  // colors
  --gutes-color-one: #{$gutes-color-one};
  --gutes-color-two: #{$gutes-color-two};
  --gutes-color-three: #{$gutes-color-three};

  // theme colors
  --gutes-color-brand: #{$gutes-color-brand};
  --gutes-color-default: #{$gutes-color-default};
  --gutes-color-primary: #{$gutes-color-primary};
  --gutes-color-secondary: #{$gutes-color-secondary};

  // Base Font Settings
  --gutes-base-font-size--xs: #{$gutes-base-font-size--xs};
  --gutes-base-font-size: #{$gutes-base-font-size};
  --gutes-base-font-size--xl: #{$gutes-base-font-size--xl};
  --gutes-base-line-height: #{$gutes-base-line-height};
  --gutes-base-font-weight: #{$gutes-base-font-weight};
  --gutes-base-font-family: #{$gutes-base-font-family};
  --gutes-base-font-color: #{$gutes-base-font-color};

  // Base Spacing Units
  --gutes-base-spacing-unit: #{$gutes-base-spacing-unit};

  // Base Border Settings
  --gutes-base-border-radius: #{$gutes-base-border-radius};
  --gutes-base-border-width: #{$gutes-base-border-width};
  --gutes-base-border-color: #{$gutes-base-border-color};

  --gutes-base-border: var(--base-border-width) solid var(--base-border-color);

  //  List Styles
  --gutes-box-shadow: #{$gutes-box-shadow};
  // title
  --gutes-font-size-title: #{$gutes-font-size-title};
  --gutes-font-weight-title: #{$gutes-font-weight-title};

  // Types
  --gutes-font-size-types: 1rem;

  // Tags and Tag-Filter
  --gutes-size-tag: 40px;
  --gutes-tag-filter-border-color: #{$gutes-tag-filter-border-color};
  --gutes-tag-filter-border-color-checked: #{$gutes-tag-filter-border-color-checked};
  --gutes-tag-filter-bgcolor: #{$gutes-tag-filter-bgcolor};

  // misc
  --gutes-size-tag-gutter: #{$gutes-size-tag-gutter};
  --gutes-grid-gap: #{$gutes-grid-gap};

  // colors
  --gutes-element-bgcolor: #{$gutes-element-bgcolor};

  // Detail Page
  // hero
  --gutes-detail-hero-overlay-color: #{$gutes-detail-hero-overlay-color};
  --gutes-detail-hero-font-color: #{$gutes-detail-hero-title-color};
  --gutes-detail-hero-text-shadow: #{$gutes-detail-hero-text-shadow};

  // Video
  --gutes-video-bgcolor: #{$gutes-video-bgcolor};

  // click and collect
  --gutes-clickcollect-bgcolor: #{$gutes-clickcollect-bgcolor};
  --gutes-clickcollect-color: #{$gutes-clickcollect-color};

  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;


  font-family: #{$gutes-base-font-family};
  font-size: #{$gutes-base-font-size};
  line-height: #{$gutes-base-line-height};
  font-weight: #{$gutes-base-font-weight};
  //background-color: var(--color-page-background);
  color: #{$gutes-base-font-color};
  overflow-y: scroll;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;

  @include media-breakpoint-down(md) {
    font-size: #{$gutes-base-font-size--xs};
  }

  @include media-breakpoint-up(xl) {
    font-size: #{$gutes-base-font-size--xl};
  }
}